import React, { Component } from 'react';
import SpellsList from '../../components/SpellsList';
import { PageHeader } from 'react-bootstrap';

class Mage extends Component {
    render() {
        return (
            <div className="container">
                <div className="row">
                    <div className="col-lg-12">
                        <PageHeader>
                            Mage
                        </PageHeader>
                    </div>
                    <div className="col-lg-12">
                        <SpellsList skills={this.props.data} />
                    </div>
                </div>
            </div>
        );
    }
}

export default Mage;

export const query = graphql `
query MageSpellList {
    allMarkdownRemark (
        sort: {fields: [frontmatter___level], order: ASC},
        filter: {fileAbsolutePath: {regex: "/(docs)/(spells)/.*\\.md$/"}}
    ) {
        edges {
            node {
                id
                html
                frontmatter {
                    title
                    syntax
                    group
                    level
                    min_mana
                    offensive
                }
            }
        }
    }
}
`


